import { getcartlist, changeCount, removecartlist } from '@/api/cart'
export default {
  namespaced: true,
  state () {
    return {
      cartlist: []
    }
  },
  getters: {
    getalllist (state) {
      return state.cartlist.length
    },
    getchecklist (state) {
      return state.cartlist.reduce((sum, item) => {
        return sum + (item.ischeck ? item.goods_num : 0)
      }, 0)
    },
    getcheckprice (state) {
      return state.cartlist.reduce((sum, item) => {
        return sum + (item.ischeck ? +item.goods.goods_price_min * item.goods_num : 0)
      }, 0).toFixed(2)
    },
    checkAll (state) {
      return state.cartlist.every(item => item.ischeck)
    }
  },
  mutations: {
    setCartList (state, newlist) {
      state.cartlist = newlist
    },
    checkbox (state, index) {
      state.cartlist[index].ischeck = !state.cartlist[index].ischeck
    },
    ischeckAll (state, check) {
      state.cartlist.forEach(item => {
        item.ischeck = check
      })
    },
    changeCount (state, { goodsId, value }) {
      state.cartlist.find(item => item.goods_id === goodsId).goods_num = value
    }
  },
  actions: {
    async getCartAction (context) {
      const { data } = await getcartlist()
      data.list.forEach(item => {
        item.ischeck = true
      })
      context.commit('setCartList', data.list)
    },
    async changeCountAction (context, obj) {
      const { goodsId, value, skuId } = obj
      context.commit('changeCount', { goodsId, value })
      await changeCount(goodsId, value, skuId)
    },
    async removecartlist (context) {
      const cartIds = []
      context.state.cartlist.forEach(item => {
        if (item.ischeck) {
          cartIds.push(item.id)
        }
      })
      await removecartlist(cartIds)
      context.dispatch('getCartAction')
    }
  }
}
