const userinfo = 'rikka_shopping_info'
const usersearch = 'rikka_search_history'

// 拿到本地用户登录key
export const getuserinfoloacl = () => {
  const result = localStorage.getItem(userinfo)
  return result === null ? { token: '', userId: '' } : JSON.parse(result)
}
// 设置本地用户登录key
export const setuserinfoloacl = (userdata) => {
  localStorage.setItem(userinfo, JSON.stringify(userdata))
}
// 删除本地用户登录key
export const removeuserinfolocal = () => {
  localStorage.removeItem(userinfo)
}

// 拿到搜索历史
export const getusersearch = () => {
  const data = localStorage.getItem(usersearch)
  return data ? JSON.parse(data) : []
}
// 设置搜索历史
export const setusersearch = (userdata) => {
  localStorage.setItem(usersearch, userdata)
}
