import { getuserinfoloacl, setuserinfoloacl } from '@/utlis/storage'

export default {
  namespaced: true,
  state: {
    userinfo: getuserinfoloacl()
  },
  getters: {

  },
  mutations: {
    setuserinfo (state, obj) {
      state.userinfo = obj
      setuserinfoloacl(obj)
    }
  },
  actions: {
    logout (context) {
      context.commit('setuserinfo', {})
      context.commit('cart/setCartList', [], { root: true })
    }
  }
}
