<template>
  <div class="home">
    <!-- 导航条 -->
    <van-nav-bar title="智慧商城" fixed />

    <!-- 搜索框 -->
    <van-search
      readonly
      shape="round"
      background="#f1f1f2"
      placeholder="请在此输入搜索关键词"
      @click="$router.push('/search')"
    />

    <!-- 轮播图 -->
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
      <van-swipe-item v-for="value in bannerdata" :key="value.imgUrl">
        <img :src = value.imgUrl alt="">
      </van-swipe-item>
    </van-swipe>

    <!-- 导航 -->
    <van-grid column-num="5" icon-size="40">
      <van-grid-item
        v-for="item in navbardata" :key="item.imgUrl"
        :icon="item.imgUrl"
        :text="item.text"
        @click="$router.push('/category')"
      />
    </van-grid>

    <!-- 主会场 -->
    <div class="main">
      <img src="@/assets/main.png" alt="">
    </div>

    <!-- 猜你喜欢 -->
    <div class="guess">
      <p class="guess-title">—— 猜你喜欢 ——</p>

      <div class="goods-list">
        <GoodsItem v-for="item in goodsdata" :key="item.goods_id" :datalist=item></GoodsItem>
      </div>
    </div>
  </div>
</template>

<script>
import GoodsItem from '@/components/GoodsItem.vue'
import { gethome } from '@/api/home'
export default {
  name: 'HomePage',
  components: {
    GoodsItem
  },
  data () {
    return {
      bannerdata: [],
      navbardata: [],
      goodsdata: []
    }
  },
  async created () {
    const homedata = await gethome()
    console.log(homedata.data.pageData)
    this.bannerdata = homedata.data.pageData.items[1].data
    this.navbardata = homedata.data.pageData.items[3].data
    this.goodsdata = homedata.data.pageData.items[6].data
  }
}
</script>

<style lang="less" scoped>

.home {
  padding-top: 100px;
  padding-bottom: 50px;
}

.van-nav-bar {
  z-index: 999;
  background-color: #c21401;
  ::v-deep .van-nav-bar__title {
    color: #fff;
  }
}
.van-search {
  position: fixed;
  width: 100%;
  top: 46px;
  z-index: 999;
}

.my-swipe .van-swipe-item {
  height: 185px;
  color: #fff;
  font-size: 20px;
  text-align: center;
  background-color: #39a9ed;
}
.my-swipe .van-swipe-item img {
  width: 100%;
  height: 185px;
}
.main img {
  display: block;
  width: 100%;
}
.guess .guess-title {
  height: 40px;
  line-height: 40px;
  text-align: center;
}
.goods-list {
  background-color: #f6f6f6;
}
</style>
